.sign-up{
    :root {
        --bs-font-sans-serif: "Noto Sans TC", sans-serif;
        --bs-font-monospace: "Azeret Mono", monospace;
      }
      
      .bg-dark {
        background-color: #1C1C1C;
      }
      
      .timeline {
        position: relative;
        &::after {
          content: '';
          position: absolute;
          top: 9px;
          left: 0;  
          right: 0;
          height: 2px;
          background-color: #C1C1C1;
        }
        &-pointer {
          position: relative;
          color: #C1C1C1;
          p {
            padding-top: 24px;
            margin-bottom: 0;
            line-height: 1.5;
          }
          &::before {
            content: '';
            position: absolute;
            left: 50%;
            transform: translateX(-50%);
            width: 20px;
            height: 20px;
            border: 1px solid #C1C1C1;
            border-radius: 50%;
            background-color: #2D2D2D;
            display: flex;
            align-items: center;
            justify-content: center;
            font-size: 12px;
            font-family: var(--bs-font-monospace);
            z-index: 1;
          }
          &.step-1::before {
            content: '1';
          }
          &.step-2::before {
            content: '2';
          }
          &.step-3::before {
            content: '3';
          }
          &.active {
            color: #62DB54;
            &::before {
              color: #FFFFFF;  
              background-color: #62DB54;
              border-color: #62DB54;
            }
          }
        }
      }
      .btn-pay{
        background:linear-gradient(104deg,#09ACF5,#62DB54) ;
      
        &:hover{
            opacity: 0.8;
        }
      }
      .payment-method{
        .btn:hover{
            color: #62DB54 !important;
            border-color: #62DB54 !important;
        }
      }
      .teacher01{
        margin-right: -48pt;
        z-index: 1;
        img{
          border: 3pt solid #62DB54 !important;
        }
        .card::after{
          content: "";
          position: absolute;
          left: 100%;
          top: 50%;
          width: 0;
          height: 0;
          border-style: solid;
          border-width: 12pt 0 12pt 12pt;
          border-color: transparent transparent transparent #2D2D2D;
        }
      }
      .list{
        margin-left: -48pt;
      }
}

@media (max-width:992px){
  .sign-up{
    .content{
        display: flex !important;
        flex-direction: column !important; 
    }
    .ps-114pt{
      padding-left: 0;
    }
    .teacher01{
      width: 100%;
      margin-left: -48pt;
      z-index: 0;
      .card::after{
        display: none;
      }
      .card::before{
        content: "";
            position: absolute;
            top: 100%;
            left: 50%;
            width: 0;
            height: 0;
            border-style: solid;
            border-width: 12pt 12pt 0 12pt;
            border-color: #2D2D2D transparent transparent transparent ;
      }
    }
    .list{
      width: 100%;
      margin-right: -48pt;
      padding-right: 0;
      .btn{
        width: 50%;
      }
      .btn-pay{
        width: 100%;
      }
    }
  }
  
}