.successful-payment{
    .icon{
        font-size: 44pt;
        line-height: 42pt;
        color: #62DB54;
    }
    .timeline {
        position: relative;
        &::after {
        content: '';
        position: absolute;
        top: 9px;
        left: 0;  
        right: 0;
        height: 2px;
        background-color: #62DB54;
        }
        &-pointer {
        position: relative;
        color: #C1C1C1;
        p {
            padding-top: 24px;
            margin-bottom: 0;
            line-height: 1.5;
        }
        &::before {
            content: '';
            position: absolute;
            left: 50%;
            transform: translateX(-50%);
            width: 20px;
            height: 20px;
            border: 1px solid #C1C1C1;
            border-radius: 50%;
            background-color: #2D2D2D;
            display: flex;
            align-items: center;
            justify-content: center;
            font-size: 12px;
            font-family: var(--bs-font-monospace);
            z-index: 1;
        }
        &.step-1::before {
            content: '1';
        }
        &.step-2::before {
            content: '2';
        }
        &.step-3::before {
            content: '3';
        }
        &.active {
            color: #62DB54;
            &::before {
            color: #FFFFFF;  
            background-color: #62DB54;
            border-color: #62DB54;
            }
        }
        }
    }
    .btn-pay{
        background:linear-gradient(104deg,#09ACF5,#62DB54) ;
        
        &:hover{
            opacity: 0.8;
        }
      }
}

@media (max-width:992px){
    .successful-payment{
        .bg-dark{
            background-color: black !important;
        }
        .pb-135pt{
            padding-bottom: 276pt !important;
        }
    }
}